import React from 'react'
import { Card, Form, Row, Col } from 'react-bootstrap'

class ProfileInfo extends React.Component{
  constructor(props){
    super(props)
    this.state = {
      name: "",
      surname: "",
      gender: "",
      region: "",
      age: 0,
      title: "",
      phone: "",
      birthday: {dmy: "", mdy: "", raw: 0},
      email: "",
      password: "",
      credit_card: {expiration: "", number: "", pin: 0, security: 0},
      photo: ""
    }
  }
  componentDidUpdate(prevProps, prevState, snapshot){
    if (this.props.customer !== prevProps.customer) {
      this.setState({...this.props.customer})
    }
  }
  handleChange(event) {
    this.setState({value: event.target.value});
  }
  render(){
    return (
      <Card>
        <Card.Header>Customer Details</Card.Header>
        <Card.Body>
          <Form>
            <Form.Group as={Row}>
              <Form.Label column sm={3}>
                First Name
              </Form.Label>
              <Col sm={9}>
                <Form.Control type="text" placeholder="First Name"
                  value={this.state.name}
                  onChange={(text)=>{this.handleChange(text)}}/>
              </Col>
            </Form.Group>
            <Form.Group as={Row}>
              <Form.Label column sm={3}>
                Last Name
              </Form.Label>
              <Col sm={9}>
                <Form.Control type="text" placeholder="Last Name"
                  value={this.state.surname}
                  onChange={(text)=>{this.handleChange(text)}}/>
              </Col>
            </Form.Group>
            <Form.Group as={Row}>
              <Form.Label column sm={3}>
                Gender
              </Form.Label>
              <Col sm={9}>
                <Form.Control type="text" placeholder="Male/Female"
                  value={this.state.gender}
                  onChange={(text)=>{this.handleChange(text)}}/>
              </Col>
            </Form.Group>
            <Form.Group as={Row}>
              <Form.Label column sm={3}>
                Region
              </Form.Label>
              <Col sm={9}>
                <Form.Control type="text" placeholder="Region"
                 value={this.state.region}
                 onChange={(text)=>{this.handleChange(text)}}/>
              </Col>
            </Form.Group>
            <Form.Group as={Row}>
              <Form.Label column sm={3}>
                Age
              </Form.Label>
              <Col sm={9}>
                <Form.Control type="text" placeholder="Age"
                 value={this.state.age}
                 onChange={(text)=>{this.handleChange(text)}}/>
              </Col>
            </Form.Group>
            <Form.Group as={Row}>
              <Form.Label column sm={3}>
                Phone
              </Form.Label>
              <Col sm={9}>
                <Form.Control type="phone" placeholder="XXX-XXX-XXXX"
                  value={this.state.phone}
                  onChange={(text)=>{this.handleChange(text)}}/>
              </Col>
            </Form.Group>
            <Form.Group as={Row}>
              <Form.Label column sm={3}>
                Email
              </Form.Label>
              <Col sm={9}>
                <Form.Control type="email" placeholder="email@address"
                  value={this.state.email}
                  onChange={(text)=>{this.handleChange(text)}}/>
              </Col>
            </Form.Group>
            <Form.Group as={Row}>
              <Form.Label column sm={3}>
                Password
              </Form.Label>
              <Col sm={9}>
                <Form.Control type="password" placeholder="Password"
                  value ={this.state.password}
                  onChange={(text)=>{this.handleChange(text)}}/>
              </Col>
            </Form.Group>
          </Form>
        </Card.Body>
      </Card>
    )
  }
}

export default ProfileInfo
